import SearchModel from '../models/SearchModel';
import ContentModel from '../models/ContentModel';
import AppView from '../views/AppView';
import SearchView from '../views/SearchView';
import ContenView from '../views/ContentView';
import NavigationView from '../views/NavigationView';

export default class App {
  constructor() {
    this.pageNumber = 1;
    this.pageClipsCount = 4;
    this.pageElementWidth = 320;
  }

  async start() {
    const page = {
      number: this.pageNumber,
      clipsCount: this.pageClipsCount,
      elementWidth: this.pageElementWidth,
    };

    const search = new SearchModel();
    const appView = new AppView();
    appView.render();
    const searchView = new SearchView();
    searchView.render();
    const navigationView = new NavigationView();
    navigationView.render();
    let model;

    const input = document.getElementById('search-input');
    const leftButton = document.getElementById('left-button');
    const rightButton = document.getElementById('right-button');
    const centerButton = document.getElementById('center-button');
    const rightHint = document.getElementById('right-button-hint');
    const leftHint = document.getElementById('left-button-hint');
    centerButton.textContent = page.number;

    function clipsCounter(width) {
      if (width < 636) {
        page.clipsCount = 1;
      } else if (width > 635 && width < 969) {
        page.clipsCount = 2;
      } else if (width > 968 && width < 1301) {
        page.clipsCount = 3;
      } else page.clipsCount = 4;
    }

    const width = document.body.clientWidth;
    clipsCounter(width);
    window.addEventListener('resize', () => clipsCounter(document.body.clientWidth));

    async function scroll(direction) {
      const slider = document.getElementById('content-list');
      if (direction === 'left' && page.number > 1) {
        page.number -= 1;
        slider.scrollLeft = (page.elementWidth * page.clipsCount * (page.number - 1));
      } else if (direction === 'right') {
        page.number += 1;
        slider.scrollLeft = (page.elementWidth * page.clipsCount * (page.number - 1));
        const loadedClipsCount = document.getElementsByClassName('content-item').length;
        const nextPageClipsCount = (page.number + 1) * page.clipsCount;
        if (loadedClipsCount < nextPageClipsCount) {
          model.nextClips();
          await model.getData();
          await model.getViewsData();
          const newClips = model.getClips();
          const newClipsView = new ContenView(newClips);
          newClipsView.render();
        }
      }

      if (page.number === 1) {
        leftButton.className += ' hidden';
        leftHint.className += ' hidden';
      } else leftButton.classList.remove('hidden');

      centerButton.textContent = page.number;
      leftHint.textContent = page.number - 1;
      rightHint.textContent = page.number + 1;
    }

    function addScrolling(slider) {
      let isDown = false;
      let startX;
      let currentX;

      slider.addEventListener('mousedown', (e) => {
        isDown = true;
        slider.classList.add('active');
        startX = e.pageX - slider.offsetLeft;
      });
      slider.addEventListener('mouseleave', () => {
        isDown = false;
        slider.classList.remove('active');
      });
      slider.addEventListener('mouseup', () => {
        isDown = false;
        slider.classList.remove('active');
        scroll(currentX > startX ? 'left' : 'right');
      });
      slider.addEventListener('mousemove', (e) => {
        if (isDown) {
          e.preventDefault();
          currentX = e.pageX - slider.offsetLeft;
        }
      });
    }

    function addSwiping(slider) {
      let isDown = false;
      let startX;
      let currentX;

      slider.addEventListener('touchstart', (e) => {
        isDown = true;
        startX = e.targetTouches[0].screenX - slider.offsetLeft;
      });
      slider.addEventListener('touchend', () => {
        isDown = false;
        scroll(currentX > startX ? 'left' : 'right');
      });
      slider.addEventListener('touchmove', (e) => {
        if (isDown) {
          e.preventDefault();
          currentX = e.targetTouches[0].screenX - slider.offsetLeft;
        }
      });
    }

    leftButton.addEventListener('click', () => scroll('left'));
    rightButton.addEventListener('click', () => scroll('right'));

    leftButton.addEventListener('mouseover', () => {
      leftHint.classList.remove('hidden');
    });
    rightButton.addEventListener('mouseover', () => {
      rightHint.classList.remove('hidden');
    });

    leftButton.addEventListener('mouseout', () => {
      leftHint.classList.add('hidden');
    });
    rightButton.addEventListener('mouseout', () => {
      rightHint.classList.add('hidden');
    });

    input.addEventListener('change', async function onchangeSearch() {
      appView.clearContent();
      search.setUrls(input.value);
      this.state = search.getUrls();
      model = new ContentModel(this.state);
      await model.getData();
      await model.getViewsData();
      const clipsData = model.getClips();
      const contentView = new ContenView(clipsData);
      contentView.render();
      const navigation = document.getElementById('navigation');
      navigation.classList.remove('hidden');

      if (page.number !== 1) {
        leftButton.className += ' hidden';
        page.number = 1;
        centerButton.textContent = page.number;
      }
      const slider = document.getElementById('content-list');
      addScrolling(slider);
      addSwiping(slider);
    });
  }
}
