export default class SearchModel {
  constructor() {
    this.query = '';
    this.key = 'AIzaSyBppxTmK1esCXLGbipsvqYArD_vPhFJzw4';
    this.snippetLink = `https://www.googleapis.com/youtube/v3/search?key=${this.key}&type=video&part=snippet&maxResults=15&q=`;
    this.snippetTokenLink = `https://www.googleapis.com/youtube/v3/search?key=${this.key}&type=video&part=snippet&maxResults=15&pageToken=`;
    this.idLink = `https://www.googleapis.com/youtube/v3/videos?key=${this.key}&id=`;
  }

  setUrls(value) {
    this.query = value;
  }

  getUrls() {
    return {
      query: this.query,
      snippetUrl: `${this.snippetLink}${this.query}`,
      tokenStart: `${this.snippetTokenLink}`,
      tokenEnd: '&q=$',
      idUrl: `${this.idLink}${this.query}`,
    };
  }
}
