/* eslint-disable class-methods-use-this */
export default class NavigationView {
  constructor() {
    this.document = document.body;
  }

  render() {
    const navigation = document.getElementById('navigation');
    const navButtons = document.createElement('div');
    navButtons.className = 'nav-buttons';
    navigation.appendChild(navButtons);

    const leftButtonHint = document.createElement('div');
    leftButtonHint.className = 'button button-hint hidden';
    leftButtonHint.setAttribute('id', 'left-button-hint');
    leftButtonHint.textContent = '0';
    navButtons.appendChild(leftButtonHint);

    const leftButton = document.createElement('div');
    leftButton.className = 'button hidden';
    leftButton.setAttribute('id', 'left-button');
    leftButton.textContent = '<';
    navButtons.appendChild(leftButton);

    const centerButton = document.createElement('div');
    centerButton.className = 'button';
    centerButton.setAttribute('id', 'center-button');
    navButtons.appendChild(centerButton);

    const rightButton = document.createElement('div');
    rightButton.className = 'button';
    rightButton.setAttribute('id', 'right-button');
    rightButton.textContent = '>';
    navButtons.appendChild(rightButton);

    const rightButtonHint = document.createElement('div');
    rightButtonHint.className = 'button button-hint hidden';
    rightButtonHint.setAttribute('id', 'right-button-hint');
    rightButtonHint.textContent = '2';
    navButtons.appendChild(rightButtonHint);
  }
}
