export default class ContentModel {
  constructor(state) {
    this.state = state;
    this.snippetData = {};
    this.staticticData = {};
    this.nextPageToken = '';
  }

  async getData() {
    const { snippetUrl } = this.state;
    const response = await fetch(snippetUrl);
    this.snippetData = await response.json();
  }

  async getViewsData() {
    let listOfIds = this.snippetData.items.reduce((acc, clip) => `${acc},${clip.id.videoId}`, []);
    listOfIds += '&part=statistics';
    const { idUrl } = this.state;
    const response = await fetch(`${idUrl}${listOfIds}`);
    this.staticticData = await response.json();
  }

  getClips() {
    const clips = [];
    this.snippetData.items.forEach((clip) => {
      const item = {};
      item.title = clip.snippet.title;
      item.link = `https://www.youtube.com/watch?v=${clip.id.videoId}`;
      item.image = clip.snippet.thumbnails.medium.url;
      item.channelTitle = clip.snippet.channelTitle;
      item.date = clip.snippet.publishedAt;
      item.description = clip.snippet.description;
      clips.push(item);
    });

    this.staticticData.items.forEach((data, index) => {
      clips[index].views = data.statistics.viewCount;
    });
    return clips;
  }

  nextClips() {
    this.nextPageToken = this.snippetData.nextPageToken;
    this.state.snippetUrl = this.state.tokenStart + this.nextPageToken + this.state.tokenEnd;
  }
}
