export default class ContentView {
  constructor(clipsData) {
    this.clipsData = clipsData;
  }

  render() {
    const contentList = document.getElementById('content-list');

    for (let clipIndex = 0; clipIndex < this.clipsData.length; clipIndex += 1) {
      const currentClip = this.clipsData[clipIndex];

      const listItem = document.createElement('div');
      listItem.className = 'content-item';
      contentList.appendChild(listItem);

      const itemInner = document.createElement('div');
      itemInner.className = 'item-inner';
      listItem.appendChild(itemInner);

      const link = document.createElement('a');
      link.className = 'item-link';
      link.setAttribute('href', currentClip.link);
      itemInner.appendChild(link);

      const title = document.createElement('div');
      title.className = 'item-title';
      const txt = document.createElement('textarea');
      txt.innerHTML = currentClip.title;
      title.textContent = txt.value;
      link.appendChild(title);

      const image = document.createElement('img');
      image.className = 'item-image';
      image.setAttribute('src', currentClip.image);
      itemInner.appendChild(image);

      const infoTitleWrapper = document.createElement('div');
      infoTitleWrapper.className = 'info-wrapper';
      itemInner.appendChild(infoTitleWrapper);

      const infoWrapper = document.createElement('div');
      infoWrapper.className = 'info-wrapper';
      itemInner.appendChild(infoWrapper);

      const channelImg = document.createElement('img');
      channelImg.className = 'channel-img icon';
      channelImg.setAttribute('src', 'https://image.flaticon.com/icons/svg/1299/1299970.svg');
      infoTitleWrapper.appendChild(channelImg);

      const channel = document.createElement('div');
      channel.className = 'item-channel';
      channel.textContent = currentClip.channelTitle;
      infoTitleWrapper.appendChild(channel);

      const dateImg = document.createElement('img');
      dateImg.className = 'date-img icon';
      dateImg.setAttribute('src', 'https://image.flaticon.com/icons/svg/1799/1799007.svg');
      infoWrapper.appendChild(dateImg);

      const date = document.createElement('div');
      date.className = 'item-date';
      const dateValue = new Date(currentClip.date);
      date.textContent = dateValue.toISOString().split('').slice(0, 10).join('');
      infoWrapper.appendChild(date);

      const viewsImg = document.createElement('img');
      viewsImg.className = 'views-img icon';
      viewsImg.setAttribute('src', 'https://image.flaticon.com/icons/svg/1799/1799577.svg');
      infoWrapper.appendChild(viewsImg);

      const viewCount = document.createElement('div');
      viewCount.className = 'item-view-count';
      viewCount.textContent = currentClip.views;
      infoWrapper.appendChild(viewCount);

      const description = document.createElement('p');
      description.className = 'item-description';
      description.textContent = currentClip.description;
      itemInner.appendChild(description);
    }
  }
}
